@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}
@mixin respond($breakpoint) {
  @if $breakpoint == sl {
    @media (min-width: 36em) { @content }; /****576px****/
  }
  @if $breakpoint == md {
    @media (min-width: 48em) { @content }; /****768px****/
  }
  @if $breakpoint == lg {
    @media (min-width: 62.5em) { @content }; /****1000px****/
  }
  @if $breakpoint == xlg {
    @media (min-width: 75em) { @content }; /****1200px****/
  }
} 
